@charset "utf-8";
@import url("https://cdn.jsdelivr.net/npm/bootstrap-icons@1.11.3/font/bootstrap-icons.min.css");

/* RESET */
html,
body,
button,
dl,
dt,
dd,
div,
form,
fieldset,
legend,
h1,
h2,
h3,
h4,
h5,
h6,
input,
select,
textarea,
ul,
ol,
li,
p,
pre,
table,
td,
th,
caption,
a,
button,
figure {
  margin: 0;
  padding: 0;
  /* line-height: 120%; */
}

html,
body {
  background-color: #fff;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: normal
}

body {
  font-size: 16px;
  color: #666666;
  font-family: font_ns, sans-serif;
}

input {
  vertical-align: middle;
  font-family: font_ns, sans-serif;
  outline: none;
}

button {
  border: none;
  cursor: pointer;
  font: inherit;
  outline: none;
}

select {
  -webkit-appearance: none;
  /* 화살표 없애기 for chrome*/
  -moz-appearance: none;
  /* 화살표 없애기 for firefox*/
  appearance: none;
  /* 화살표 없애기 공통*/
}

select::-ms-expand {
  display: none;
  /* 화살표 없애기 for IE10, 11*/
}

ol,
ul {
  list-style: none
}

a {
  text-decoration: none;
  color: #333;
}

img {
  vertical-align: top;
  border: 0
}

table {
  border-collapse: collapse;
  border-spacing: 0;
  width: 100%;
}

caption {
  visibility: hidden;
  font-size: 0;
  line-height: 0;
  opacity: 0;
  height: 0;
  overflow: hidden;
}

em {
  font-style: normal;
}

#root {
  width: 100%;
}