.header-wrap {
  width: 100%;
  position: relative;
}
.header-title-wrap {
  position: relative;
  height: 165px;
  }
.header-title {
  border-top: 5px solid #009223;
  height: 165px;
  position: absolute;
  left: 0;
  width: 100%;
  top: 0;
  z-index: 99;
  background-color: #fff;
  overflow: hidden;
  min-width: 1280px;
  border-radius: 5px 0px 0px 0px;
}

.header-logo {
  position: absolute;
  left: 50%;
  top: 35px;
  margin-left: -101px;
  transition-duration: 0.3s
}

.header-logo a {
  display: block;
  background: url(../../image/logo_w.png) 0 0 no-repeat;
  width: 202px;
  height: 40px;
  overflow: hidden;
  font-size: 0px;
  line-height: 0px;
  text-indent: -9999px;
}

.header-util_menu {
  position: absolute;
  right: 25px;
  top: 40px;
}

.header-util_menu ul li {
  float: left;
  position: relative;
}

.header-util_menu ul li~li {
  margin-left: 30px;
}

.header-util_menu ul li a {
  display: block;
  color: #666666;
  font-size: 13px;
  font-weight: normal;
  line-height: 30px;
  height: 30px;
}

.bi-globe {
  font-size: 20px;
  font-weight: bold;
}

.header-main-menu {
  position: relative;
  height: 55px;
  margin: 0 auto;
  border-bottom: 0.5px solid #efebeb;
}

.header-main-menu>ul {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: center;
  height: 44px;
}

.header-main-menu>ul>li {
  width: 165px;
  font-weight: bold;
  font-size: 20px;
  text-align: center;
  color: black;
  cursor: pointer;
}

.header-main-menu>ul>li:hover {
  color: #009223;
}